import { React, useState } from "react";
import PropTypes from 'prop-types';
import classNames from "classnames";

const Product = ({ product }) => {

  const availableTypes = ['тонкое', 'традиционное']
  const availableSizes = [26, 30, 40]

  const [productType, setProductType] = useState(product.types[0])
  const [productSize, setProductSize] = useState(product.sizes[0]);

    const onSelectType = index => {
        setProductType(index)
    }
    const onSelectSize = index => {
        setProductSize(index)
    }

  return (
    <div className="catalog__grid-item product">
      <img className="product__img" src={product.imageUrl} alt="pizza" />
      <p className="product__title">{product.name}</p>
      <div className="product__settings">
        <div className="product__base">
            {
                availableTypes.map((typeName, index) => (
                    <button 
                        key={index + '_' + typeName}
                        onClick={() => onSelectType(index) } 
                        className={classNames({
                            active: productType === index,
                            disabled: !product.types.includes(index)
                        })}
                    >
                        { typeName }
                    </button>
                ))
            }
        </div>
        <div className="product__size">
            {
                availableSizes.map((size, index) => (
                    <button 
                        key={index + '_' + size}
                        onClick={() => onSelectSize(index) } 
                        className={classNames({
                            active: productSize === index,
                            disabled: !product.sizes.includes(size)
                        })}
                    >
                        { size } см.
                    </button>
                ))
            }
        </div>
      </div>
      <div className="product__total">
        <div className="product__price">от {product.price} грн.</div>
        <button className="product__buy btn btn-bordered">
          <svg
            width="12"
            height="12"
            viewBox="0 0 12 12"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z"
              fill="#EB5A1E"
            />
          </svg>
          Добавить
          {false && <span>2</span>}
        </button>
      </div>
    </div>
  );
};

Product.propTypes = {
    name: PropTypes.string,
    imageUrl: PropTypes.string,
    price: PropTypes.string,
    types: PropTypes.arrayOf(PropTypes.number).isRequired,
    sizes: PropTypes.arrayOf(PropTypes.number).isRequired,
};

Product.defaultProps = {
    name: 'Pizza\'s name',
    price: 0,
    imageUrl: 'https://thumbs.dreamstime.com/b/one-slice-pizza-icon-clipart-image-isolated-white-background-one-slice-pizza-icon-141884890.jpg',
    types: [],
    sizes: []
}

export default Product;
