import React from 'react'
import { Link } from 'react-router-dom'

import emojiImg from './../../assets/img/emoji.svg'
import emptyImg from './../../assets/img/cart-empty.png'

const Cart = () => {
    return (
        <section className="cart cart--empty">
            <h1 className="cart__title">Корзина пустая <img src={ emojiImg } alt="empty" /></h1>
            <p className="cart__text">Вероятней всего, вы не заказывали ещё пиццу.</p>
            <p className="cart__text">Для того, чтобы заказать пиццу, перейди на главную страницу.</p>
            <img className="cart__img" src={ emptyImg } alt="empty" />
            <Link to="/" className="cart__go-back btn btn-dark">Вернуться назад</Link>
        </section>
    )
}

export default Cart
