import { React, useState, useEffect, useRef } from 'react';
import arrow from './../../assets/img/arrow-up.svg';

const Navigation = ({categories, sortItems}) => {

    const [ sortIsActive, setActiveSort ] = useState(false)
    const [ activeCat , setActiveCat ] = useState(null)
    const [ activeSortItem , setActiveSortItem ] = useState(0)

    const clickOutsideDd = (e) => {
        if (!e.path.includes(sortRef.current)) {
            setActiveSort(false);
        }
    }

    const onSelectCat = (index) => {
        setActiveCat(index)
    }
    const onSelectSortItem = (index) => {
        setActiveSortItem(index)
    }

    const sortRef = useRef()
    const sortLabel = sortItems[activeSortItem]

    useEffect(() => {
        document.body.addEventListener('click', clickOutsideDd)
    }, []);

    return (
        <nav className="nav">
            <ul className="categories">
                <li 
                    className={`categories__item btn ${activeCat === null ? 'active' : '' }`}
                    onClick={() => onSelectCat(null)}
                >
                    Все
                </li>
                {categories.map( (category, index) => (
                    <li 
                        className={`categories__item btn ${activeCat === index ? 'active' : '' }`}
                        key={`${index}_${category}`}
                        onClick={() => onSelectCat(index)}
                    >
                        {category}
                    </li>
                ))}
            </ul>

            <div className={`sort ${sortIsActive ? 'active' : ''}`}>
                <img src={ arrow } alt="arrow"/>
                <span>Сортировка по:</span>
                <button onClick={() => setActiveSort(!sortIsActive)} ref={sortRef}>{ sortLabel }</button>
                { sortIsActive && (
                    <div className="sort__dropdown">
                        <ul>
                            {sortItems && sortItems.map((item, index) => (
                                <li 
                                    className={activeSortItem === index ? 'active' : ''}
                                    key={`${index}_${item}`}
                                    onClick={() => onSelectSortItem(index)}
                                >
                                    {item}
                                </li>
                            ))}
                        </ul>
                    </div>
                )}
            </div>
        </nav>
    )
}

export default Navigation
