import { React, useState, useEffect } from "react";
import { Routes, Route } from "react-router-dom";
import axios from 'axios'

import { Header, Cart, Catalog } from './components'

import './App.scss';
import logo from './assets/img/logo.png'


function App() {

  const [products, setProducts] = useState([])

  useEffect(() => {
    axios.get('http://localhost:3000/db.json').
    then(({ data }) => {
      setProducts(data.pizzas)
    })
  }, [])

  const shopInfo = {
    shopName: 'REACT PIZZA',
    shopDescr: 'самая вкусная пицца во вселенной',
    logo: logo
  }

  return (
    <div className="app">

      <Header shopInfo={shopInfo}/>

      <Routes>
        <Route exact path="/" element={
          <Catalog 
            products={products} 
            categories={['Мясные', 'Вегетарианские', 'Гриль', 'Острые', 'Закрытые',]} 
            sortItems={['популярности', 'цене', 'алфавиту']} 
          />
        } />
        <Route exact path="/cart" element={<Cart />} />
      </Routes>

    </div>
  );
}

export default App;
